import React, { Component } from 'react'
import { Input, Button, Form, message } from 'antd'
const FormItem = Form.Item
import { login } from '/api/login'
import { hashHistory } from 'react-router'

class Login extends Component {

	state = {
		lock: false
	}

	submit = async  _ => {
		if (!this.state.lock) {
			await this.setState({lock: true})
			this.props.form.validateFieldsAndScroll(null, {}, (err, val) => {
				if (!err || !Object.getOwnPropertyNames(err).length) {
					let params = Object.assign({}, val)

					login(params)
					.then(async res => {
						await this.setState({lock: false})
						if (res.code == 200) {
							message.success('登录成功')
							let user = res.data
							await localStorage.setItem('token', user.token)
							await localStorage.setItem('userid', user.userId)
							await localStorage.setItem('username', user.userName)
							if(window.LOGIN_LAST_PATH){
								hashHistory.push(window.LOGIN_LAST_PATH)
							}else{
								hashHistory.push('/')
							}
						}
					})
				} else {
					this.setState({lock: false})
				}
			})
		}
	}

	render = _ => {
		const { getFieldDecorator } = this.props.form

		return <div className='loginWrapper'>
			<div className='loginContent'>
				<span className='logintitle'>投标IMS登录</span>
				<FormItem>
					{getFieldDecorator('userName', {
						rules: [{
					        required: true, message: '用户名不能为空!',
					    }]
				    })(<Input type='text'/>)}
				</FormItem>
				<FormItem>
					{getFieldDecorator('password', {
						rules: [{
					        required: true, message: '密码不能为空!',
					    }]
				    })(<Input type='password'/>)}
				</FormItem>
				<Button 
				onClick={this.submit}
				type='primary'>登录</Button>
			</div>
		</div>
	}
}

const LoginForm = Form.create()(Login)
export default LoginForm


